import { AppSidebar, AppSidebarTab, Actions, Multiselect, ActionInput, DatetimePicker } from '@nextcloud/vue';
import { generateUrl } from 'nextcloud-router';
export default {
  name: 'Sidebar',
  components: {
    Actions: Actions,
    ActionInput: ActionInput,
    AppSidebar: AppSidebar,
    AppSidebarTab: AppSidebarTab,
    Multiselect: Multiselect,
    DatetimePicker: DatetimePicker
  },
  props: {
    show: Boolean
  },
  data: function data() {
    return {
      date: new Date(),
      tedit: false,
      valid: true,
      _status: 'Draft',
      luvers: null,
      url: null,
      value: null,
      loading: false,
      options: ['admin', 'user1', 'user2', 'guest', 'group1'],
      type: ['Banner', 'plenka', 'Bymaga'],
      material: '',
      materialtype: '',
      list: {
        Banner: [{
          value: 'litoj',
          title: ''
        }, {
          value: 'perfo',
          title: ''
        }],
        Plenka: [{
          value: 'glance',
          title: ''
        }, {
          value: 'mat',
          title: ''
        }, {
          value: 'perfo',
          title: ''
        }],
        Bumaga: [{
          value: 'blubeck',
          title: ''
        }, {
          value: 'city',
          title: ''
        }]
      },
      lang: {
        formatLocale: {
          // MMMM
          months: ['', '', '', '', '', '', '', '', '', '', '', ''],
