          firstDayOfWeek: 1,
          // first week contains January 1st.
          firstWeekContainsDate: 1
        },
        monthBeforeYear: false
      },
      wwidth: null,
      test: null
    };
  },
  computed: {
    createDate: function createDate() {
      var options = {
        weekday: 'long',
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        hour12: false
      };
      return 'Created: ' + this.date.toLocaleDateString('ru-RU', options);
    },
    formatedOptions: function formatedOptions() {
      return this.options.map(function (item) {
        return {
          user: item,
          displayName: item,
          // desc: `This is the ${item.startsWith('group') ? 'group' : 'user'} ${item}`,
          icon: item.startsWith('group') ? 'icon-group' : 'icon-user',
          isNoUser: item.startsWith('group')
        };
      });
    },
    menu: function menu() {
      return [{
        id: 'app-category-your-apps',
        classes: [],
        href: '#1',
        // action: this.log,
        icon: 'icon-category-installed',
        text: t('settings', 'Your apps')
      }, {
        caption: true,
        text: t('vueexample', 'Section')
      }, {
        id: 'app-category-enabled',
        classes: [],
        icon: 'icon-category-enabled',
        href: '#2',
        utils: {
          actions: [{
            icon: 'icon-delete',
            text: t('settings', 'Remove group'),
            action: function action() {
              alert('remove');
            }
          }]
        },
        text: t('settings', 'Active apps')
      }, {
        id: 'app-category-enabled',
        classes: [],
        href: '#3'
      }, {
        id: 'app-category-disabled',
        classes: [],
        icon: 'icon-category-disabled',
        href: '#4',
        undo: true,
        text: t('settings', 'Disabled apps')
      }];
    }
  },
  mounted: function mounted() {
    var vm = this;
    var el = document.querySelector('.app-sidebar-header__desc');
    el.addEventListener('click', function () {
      vm.tedit = true;
      console.log('title clicked');
    }); // document.querySelector('.app-sidebar-header__desc > a').classList.toggle('MyClass')
    // document.querySelector('.app-sidebar-header__desc > a').className = 'MyClass'

    vm.wwidth = el.offsetWidth + 0;
    console.log(this.$store.state.whoIs);
  },
  methods: {
    pickImage: function pickImage() {
      var vm = this;
      OC.dialogs.filepicker('Path to your Recipe Image', function (path) {
        vm.url = generateUrl('/core/preview?fileId=7&x=1680&y=1050&a=true');
        console.log(path); // console.log(url)
        // $('input[name="image"]').val(path)
      }, false, ['image/jpeg', 'image/png'], true, OC.dialogs.FILEPICKER_TYPE_CHOOSE);
    },
    showData: function showData() {
      console.log(this.value);
    },
    titleInput: function titleInput() {
      console.log('Title input event');
    },
    fedit: function fedit() {
