import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    whoIs: true,
    settings: {},
    tasks: []
  },
  getters: {
    numTasks: function numTasks(state) {
      return function () {
        return state.tasks.length;
      };
    },
    getTask: function getTask(state) {
      return function (id) {
        if (state.notesIds[id] === undefined) {
          return null;
        }

        return state.notesIds[id];
      };
    }
  },
  mutations: {},
  actions: {},
  modules: {}
});